.div-info {
	position: absolute;
	top: 24px;
	left: 24px;
	z-index: 100000;
	max-width: 12%;
	margin: 24px;
	padding: 24px;
	border-radius: 24px;
	background: white;
	opacity: 0.9;
}

.div-points {
	font-size: 12px;
}

.div-points-info {
	margin-top: 12px;
}

.div-points-data {
	margin-top: 12px;
	padding: 12px;
	border-radius: 12px;
	background: lightgray;
}

.button-copy-to-cb {
	margin-top: 12px;
	padding: 12px;
	border: none;
	border-radius: 12px;
	font-size: 9px;
}

.div-info-distance {
	margin-bottom: 12px;
}

.button-copy-to-cb:hover {
	color: red;
}
